.main-nav__btn {
  z-index: 999;
  position: fixed;
  top: 0.5em;
  right: 0.5em;
  background: transparent;
  width: 45px;
  height: 35px;
  outline: none;
  border: none;
  padding-left: 5px;
  padding-right: 5px;
}
@media only screen and (min-width: 1000px) {
  .main-nav__btn {
    display: none;
  }
}
.main-nav__btn span {
  transition: all 0.3s ease-in-out;
  display: block;
  opacity: 1;
  min-height: 3px;
  background: #d7103b;
  margin-bottom: 5px;
}
.main-nav__btn:hover span, .main-nav__btn:focus span {
  background: #f35a7b;
}

.main-nav__btn--open span {
  background: #d7103b;
}
.main-nav__btn--open span:nth-child(2) {
  opacity: 0;
  display: none;
}
.main-nav__btn--open span:first-child {
  transform: rotate(45deg);
}
.main-nav__btn--open span:last-child {
  transform: rotate(-45deg) translate(5px, -5px);
}

.main-nav__list {
  opacity: 0;
  z-index: 998;
  position: fixed;
  top: 0px;
  left: 150vw;
  width: 100%;
  height: 100%;
  background: #1e2d32;
  list-style: none;
  text-align: center;
  font-size: 2.5rem;
  font-weight: 600;
  padding-top: 0.5em;
  padding-left: 2.3em;
  padding-right: 2.3em;
}
@media only screen and (min-width: 1000px) {
  .main-nav__list {
    position: relative;
    opacity: 1;
    top: initial;
    left: initial;
    width: auto;
    height: auto;
    background: transparent;
    padding-top: 0.5em;
    padding-left: 0.5em;
    padding-right: 0em;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.main-nav__list.main-nav__list--open {
  transition: all 0.6s ease-in-out;
  animation: show-nav 0.6s ease-in-out forwards;
}
@media only screen and (min-width: 1000px) {
  .main-nav__list.main-nav__list--open {
    animation: none;
  }
}

@keyframes show-nav {
  0% {
    opacity: 0;
    left: 150vw;
  }
  100% {
    opacity: 1;
    left: 0px;
  }
}
.main-nav__item {
  margin-bottom: 0.3em;
}
@media only screen and (min-width: 1000px) {
  .main-nav__item {
    margin-left: 1em;
  }
}

.main-nav__link {
  transition: all 0.3s ease-in-out;
  display: block;
  text-decoration: none;
  color: #fbc237;
  outline: none;
}
@media only screen and (min-width: 1000px) {
  .main-nav__link {
    color: #fff;
  }
}
.main-nav__link:focus, .main-nav__link:hover {
  color: #d7103b;
}
.main-nav__link:active {
  color: #f68aa1;
}

